.players {
  text-align: center;
  margin-bottom: 0.5rem;
  margin-top: 1.2rem;
}

.player-white,
.player-black {
  font-size: 1.1em;
  font-weight: 600;
  color: #333;
  margin: 0.2rem 0;
}

.player-white {
  color: #2c3e50;
}

.player-black {
  color: #34495e;
}

.event {
  font-size: 0.9em;
  color: #666;
  text-align: center;
  margin-bottom: 0.25rem;
}

.date {
  font-size: 0.85em;
  color: #888;
  text-align: center;
  margin-bottom: 1.3rem;
}

.error {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 200px;
  font-size: 1.1em;
  color: #d32f2f;
}

.game-controls {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  width: 100%;
  max-width: 400px;
  margin: 1rem auto 0 auto;
  padding-left: 10px;
  padding-right: 10px;
  box-sizing: border-box;
}

.control-button {
  width: 40px;
  height: 40px;
  border: none;
  border-radius: 50%;
  background-color: #f0f0f0;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s;
  border: 1.5px solid #b1b1b1;
  box-shadow: 0 1px 4px rgba(0,0,0,0.04);
}

.control-button:hover:not(:disabled) {
  background-color: #e0e0e0;
  transform: scale(1.05);
  border-color: #bdbdbd;
}

.control-button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.control-button svg {
  width: 24px;
  height: 24px;
}

.control-buttons-container {
  display: flex;
  flex-direction: column;
  gap: 8px;
  align-items: center;
}

.move-counter {
  font-size: 0.75rem;
  color: #666;
  font-weight: 500;
  text-align: center;
  margin-top: 4px;
}

.side-button-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 4px;
  min-width: 55px;
}

.side-button-label {
  font-size: 0.75rem;
  color: #666;
  font-weight: 500;
  text-align: center;
  margin-top: 2px;
}

.control-button.side-button {
  width: 40px;
  height: 40px;
  background-color: #f0f0f0;
  border: 1.5px solid #b1b1b1;
  box-shadow: 0 1px 4px rgba(0,0,0,0.04);
}

.control-button.side-button:hover:not(:disabled) {
  background-color: #e0e0e0;
  transform: scale(1.05);
  border-color: #bdbdbd;
}

.control-button.side-button svg {
  width: 18px;
  height: 18px;
}

.side-button-spacer {
  width: 40px;
  height: 40px;
}

.Loading {
  display: flex;
  justify-content: center;
  height: 100%;
  flex: 1;
  margin-bottom: 20px;
}