.App {
  max-width: 100%;
  margin: 0 auto;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-sizing: border-box;
  width: 100%;
  overflow-x: hidden;
  margin-bottom: 2rem;
}

.Loading {
  display: flex;
  justify-content: center;
  height: 100%;
  flex: 1;
  margin-bottom: -10px;
}

.chessboard-info {
  display: flex;
  width: 100%;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
  max-width: 400px;
}

.level-display {
  text-align: left;
  font-size: 1.2em;
  padding: 3px 0;
  color: #2f74ab;
  text-align: left;
  margin: 2px 0;
  font-weight: bold;
}

.level {
  text-align: "right";
  margin-right: 15px;
  color: "#000";
  font-size: 1.15em;
}

.turn-indicator-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2px;
}

.turn-label {
  font-size: 0.7rem;
  color: #666;
  font-weight: 500;
}

.chessboard-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  max-width: 400px;
  margin: 0 auto;
  box-sizing: border-box;
}
